import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';

import { productol } from '../modelo_producto/modelo_productol';


@Component({
  selector: 'app-productol',
  templateUrl: './productol.page.html',
  styleUrls: ['./productol.page.scss'],
})
export class ProductolPage implements OnInit {

  objeto:productol = { tipo:"", nombreali:"", cantidad_pro:"", cantidad_min:"", cantidad_med:"", cantidad_max:""}

  pruebas: productol[] = [];
  url = 'http://192.168.4.153/serve_api/crear_productol.php';
  url3 = 'http://192.168.4.153/serve_api/menu_productol.php';
  
  disabledButton;

  constructor(
    public http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders,
    private actRoute: ActivatedRoute
  ) { 
    this.listarRegistroPA();
  }

  ngOnInit() {
  }

  agregarRegistroPro(obj :productol): Observable<productol>{
    return this.http.post<productol>(this.url, JSON.stringify(obj))
  }

  async agregarPRO() {
    this.agregarRegistroPro(this.objeto)
    .subscribe((resp) =>{
      this.objeto.tipo='';
      this.objeto.nombreali='';
      this.objeto.cantidad_pro='';
      this.objeto.cantidad_min='';
      this.objeto.cantidad_med='';
      this.objeto.cantidad_max='';
      this.listarRegistroPA();
      console.log(resp)
    })
    const toast = await this.toastCtrl.create({
      message: "Registro Exitoso",
      duration: 1500
    });
    toast.present();
  }

  getRegistroPA(): Observable<any>{
    return this.http.get(this.url3);
  }

  listarRegistroPA(){
    this.pruebas = [];
    this.getRegistroPA().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas.push(res[i]);
      }
    })
  }


  async delData(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'del_productoL',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarRegistroPA();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }

  abrirCrud(a){
    this.router.navigate(['/modificarprol/'+a]);
  }



}
